export default ".register {\n    width: 400px;\n    margin: 16px auto;\n    font-size: 16px;\n}\n/* Reset top and bottom margins from certain elements */\n.register-header,\n.register p {\n    margin-top: 0;\n    margin-bottom: 0;\n}\n/* The triangle form is achieved by a CSS hack */\n.register-triangle {\n    width: 0;\n    margin-right: auto;\n    margin-left: auto;\n    border: 12px solid transparent;\n    border-bottom-color: #23C185;\n}\n.register-header {\n    background: #23C185;\n    padding: 20px;\n    font-size: 1.4em;\n    font-weight: normal;\n    text-align: center;\n    text-transform: uppercase;\n    color: #fff;\n}\n.register-container {\n    background: #ebebeb;\n    padding: 12px;\n}\n.register-bg {\n    content: url(/src/main/front-end/src/assets/img/SESCAM1.png);\n    position: relative;\n    top: 0%;\n    left: -60%;\n}\n/* Every row inside .login-container is defined with p tags */\n.register p {\n    padding: 12px;\n}\n.register input {\n    box-sizing: border-box;\n    display: block;\n    width: 100%;\n    border-width: 1px;\n    border-style: solid;\n    padding: 16px;\n    outline: 0;\n    font-family: inherit;\n    font-size: 0.95em;\n}\n.register input[type=\"email\"],\n.register input[type=\"password\"] {\n    background: #fff;\n    border-color: #bbb;\n    color: #555;\n}\n/* Text fields' focus effect */\n.register input[type=\"email\"]:focus,\n.register input[type=\"password\"]:focus {\n    border-color: #888;\n}\n.register input[type=\"submit\"] {\n    background: #23C185;\n    border-color: transparent;\n    color: #fff;\n    cursor: pointer;\n}\n.register input[type=\"submit\"]:hover {\n    background: #17c;\n}\n/* Buttons' focus effect */\n.register input[type=\"submit\"]:focus {\n    border-color: #05a;\n}\n/*# sourceMappingURL=data:application/json;base64, */"